.Header {
    margin-top: .5rem;
    padding-left: 10px;
}

.Header ul li {
    list-style: none;
    margin: 0;
    padding: .5rem 1rem;
    border-radius: 3px;
}

.Header ul li:hover {
    background-color: #444;
}

.Header a:hover {
    text-decoration: none;
}

.Header h3 a {
    color: white;
}

.Header h3 a:hover {
    color: #00bc8c;
}
.item-list .list-group-item {
    cursor: pointer;
}

.item-list .list-group-item:hover {
    background-color: #444;
}
@keyframes lds-double-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@-webkit-keyframes lds-double-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}

@-webkit-keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}

.lds-css {
    margin: auto;
}

.lds-double-ring {
    position: relative;
}

.lds-double-ring div {
    position: absolute;
    width: 100px;
    height: 100px;
    top: 20px;
    left: 20px;
    border-radius: 50%;
    border: 8px solid #000;
    border-color: #d11a1a transparent #d11a1a transparent;
    -webkit-animation: lds-double-ring 2.3s linear infinite;
    animation: lds-double-ring 2.3s linear infinite;
}

.lds-double-ring div:nth-child(2) {
    width: 80px;
    height: 80px;
    top: 30px;
    left: 30px;
    border-color: transparent #545454 transparent #545454;
    -webkit-animation: lds-double-ring_reverse 2.3s linear infinite;
    animation: lds-double-ring_reverse 2.3s linear infinite;
}

.lds-double-ring {
    width: 123px !important;
    height:123px; 
    -webkit-transform: translate(-100px, -100px) scale(1) translate(100px, 100px);
    transform: translate(-100px, -100px) scale(1) translate(100px, 100px);
}
